// import module
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { ContactModule } from './contacts/contacts.module';

// import component
import { ContactDetailComponent } from './contact-detail/contact-detail.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';

// Config path
const routesConfig: Routes = [
    { path: 'detail/:id/:name/:phoneNumber', component: ContactDetailComponent },
    { path: '', redirectTo: '/contacts', pathMatch: 'full' }, // redirect to default page
    { path: '**', component: PageNotFoundComponent } // redirect to not found page
];

@NgModule({
    imports: [
        // Note: this is child module, so it always stands in front of the root module (the module .forRoot) 
        ContactModule, 
        RouterModule.forRoot(routesConfig),
        CommonModule
    ],
    declarations: [
        PageNotFoundComponent,
        ContactDetailComponent
    ],
    exports: [
        RouterModule
    ]
})

export class AppRoutingModule{}


