import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html',
  styleUrls: ['./user-form.component.css']
})
export class UserFormComponent implements OnInit {
  name = '';
  even = { color: 'red', fontSize: '40px'};
  odd = { color: 'black', fontSize: '20px'};
  classCircle = { circle: true};
  classSquare = { square: true};
  arrSubjects = ['NodeJS', 'AngularJS'];

  constructor() { }

  ngOnInit() {
  }

  highlight(){
    if(this.name.length % 2 == 0){
      return true;
    } else {
      return false;
    }
  }

  demoNgStyle() {
    if(this.name.length % 2 == 0){
      return this.even;
    } else {
      return this.odd;
    }
  }

  demoNgClass(){
    if(this.name.length % 2 == 0){
      return this.classCircle;
    } else {
      return this.classSquare;
    }
  }

  demoNgIf(){
    if(this.name.length % 2 == 0){
      return true;
    } else {
      return false;
    }
  }

  demoNgFor(){
    if(this.name.length % 2 == 0){
      return this.arrSubjects;
    } 
  }

}



